import React from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import styled, {ThemeProvider} from "styled-components";
import {WidthStyle} from "./widget/style/width_style";
import {theme} from "./theme";
import {H2Widget, H1Widget, H3Widget, H4Widget, H5Widget} from "./view/home";
import Im1 from "./assets/images/woodchuck_logo.png"
import {SimpleDialog} from "./view/model/tis";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            backgroundColor: "#A76643",
            height: "64px"
        },
        title: {
            flexGrow: 1,

        },
    }),
);

function App() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = (value: string) => {
        setOpen(false);
    };
    return (
        <ThemeProvider theme={theme}>
            <div className="App">
                <AppBar position="fixed" className={classes.root}>
                    <Toolbar variant="dense" className={classes.title}>
                        <BarInner>
                            <img src={Im1} alt=""/>
                            <div>
                                <p onClick={handleClickOpen}>How to buy?</p>
                                <p>Telegram</p>
                                <p>Buy on PancakeSwap</p>
                            </div>
                        </BarInner>
                    </Toolbar>
                </AppBar>
                <H1Widget/>
                <H2Widget/>
                <H3Widget/>
                <H4Widget/>
                <H5Widget/>
            </div>
            <SimpleDialog  open={open} onClose={handleClose} />
        </ThemeProvider>

    );
}

const BarInner = styled.div`
  ${() => WidthStyle.MaxWidth};
  display: flex;
  justify-content: space-between;
  align-items: center;

  p {
    margin-left: 40px;
    font-size: 18px;
    font-weight: 500;
    color: #FFFFFF;
    line-height: 25px;
    cursor: pointer;
  }
  div{
    display: flex;
    justify-content: flex-end;
  }

  @media screen and (max-width: ${({theme}) => theme.sm}) {
    p {
      font-size: 12px;
      margin-left: 20px;
    }
    flex-flow: column;
    padding: 5px 0;
    box-sizing: border-box;
  }
`

export default App;


